import React from 'react';
import RetailMakeTransactionContainer from '../../../components/Retail/RetailBuySell/RetailMakeTransactionContainer';
import RetailOrderOverviewContainer from '../../../components/Retail/RetailBuySell/RetailOrderOverviewContainer';
import { getBEMClasses } from '../../../helpers/cssClassesHelper';
import RetailBalancesContainer from '../../../components/Retail/RetailBuySell/RetailBalancesContainer';
import BuySellChartOverviewContainer from '../../../components/Retail/RetailBuySell/BuySellChartOverview/BuySellChartOverviewContainer';

import './RetailBuySellDesktopLayout.css';

const classes = getBEMClasses('retail-buy-sell-layout');

const RetailBuySellDesktopLayout = () => {
  return (
    <div className={classes()}>
      <div className={classes('layout-header')} />
      <div className={classes('content-wrapper')}>
        <div className={classes('col', ['lg'])}>
          <div
            className={classes(
              'make-transaction-container container-with-shadow'
            )}>
            <div className={classes('make-transaction-col-1 ')}>
              <RetailMakeTransactionContainer />
            </div>

            <div className={classes('make-transaction-col-2')}>
              <div className={classes('order-overview-container')}>
                <RetailOrderOverviewContainer />
              </div>

              <hr className={classes('right-panel-divider')} />

              <div className={classes('balances-container ')}>
                <RetailBalancesContainer />
              </div>
            </div>
          </div>
        </div>

        <div
          className={`${classes('col', ['sm', 'with-padding'])}
          container-with-shadow`}>
          <BuySellChartOverviewContainer />
        </div>
      </div>
    </div>
  );
};

export default RetailBuySellDesktopLayout;



